@import url(https://fonts.googleapis.com/css?family=Exo:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



*{
    margin: 0px;
    padding: 0px;
}

body{
    font-family: 'Exo', sans-serif;
}

.navigation-button{
    position: absolute;
    top:90vh;
    right: 5vh;
    -webkit-transform: rotateZ(90deg);
            transform: rotateZ(90deg);
}

.navigation-bar-button{
    width: 100%;
    background-color: black;
    display: none;
}

.context {
    width: 100%;
    position: absolute;
    top:40vh;
    color: #fff;
    text-align: center;
}
.context h1{
    font-size: 50px;
}


.area{
    background: #4e54c8;  
    background: -webkit-linear-gradient(to left, #8f94fb, #4e54c8);  
    width: 100%;
    height:100vh;
    
   
}

.circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    -webkit-animation: animate 25s linear infinite;
            animation: animate 25s linear infinite;
    bottom: -150px;
    
}

.circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}


.circles li:nth-child(2){
    left: 10%;
    width: 20px;
    height: 20px;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-animation-duration: 12s;
            animation-duration: 12s;
}

.circles li:nth-child(3){
    left: 70%;
    width: 20px;
    height: 20px;
    -webkit-animation-delay: 4s;
            animation-delay: 4s;
}

.circles li:nth-child(4){
    left: 40%;
    width: 60px;
    height: 60px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-duration: 18s;
            animation-duration: 18s;
}

.circles li:nth-child(5){
    left: 65%;
    width: 20px;
    height: 20px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

.circles li:nth-child(6){
    left: 75%;
    width: 110px;
    height: 110px;
    -webkit-animation-delay: 3s;
            animation-delay: 3s;
}

.circles li:nth-child(7){
    left: 35%;
    width: 150px;
    height: 150px;
    -webkit-animation-delay: 7s;
            animation-delay: 7s;
}

.circles li:nth-child(8){
    left: 50%;
    width: 25px;
    height: 25px;
    -webkit-animation-delay: 15s;
            animation-delay: 15s;
    -webkit-animation-duration: 45s;
            animation-duration: 45s;
}

.circles li:nth-child(9){
    left: 20%;
    width: 15px;
    height: 15px;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-animation-duration: 35s;
            animation-duration: 35s;
}

.circles li:nth-child(10){
    left: 85%;
    width: 150px;
    height: 150px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-duration: 11s;
            animation-duration: 11s;
}



@-webkit-keyframes animate {

    0%{
        -webkit-transform: translateY(0) rotate(0deg);
                transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        -webkit-transform: translateY(-1000px) rotate(720deg);
                transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}



@keyframes animate {

    0%{
        -webkit-transform: translateY(0) rotate(0deg);
                transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        -webkit-transform: translateY(-1000px) rotate(720deg);
                transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}

a:link,
a:visited,
a:hover,
a:active{
    color: black;
    text-decoration: none;
}

@media screen and (max-width: 500px) {
    h1{
        padding-left: 0.1rem;
        padding-right: 0.1rem;
    }
    .navigation-button{
        display: none;
    }
    .navigation-bar-button{
        display: block;
        text-align: center;
        height : 3rem;
        position: absolute;
        bottom: 0;
    }
    .navigation-bar-button-link{
        color: white;
        position: relative;
        top : 1vh;
    }
}

.about-me{
    background-color: #EEA47FFF;
    background-color: white;
    color: #4e54c8;
    text-align: center;
    padding: 4rem;
    padding-left: 5rem;
    padding-right: 5rem;
}
.about-me h1{
    padding-bottom:1rem;
}
.about-me hr{
    margin-top:2.5rem;
    margin-bottom:2.5rem;
}
.about-me-tab-div{
    display: flex;
    justify-content: space-around;
    text-align: left;
}
.about-me-tab-div div{
    width: 25%;
    display: inline-block;
    padding: 1rem;
}
.about-me-tab-div img{
    width: 100%;
}
@media screen and (max-width: 500px) {
    .about-me{
        padding: 2rem;
        padding-left: 1rem;
        padding-right: 1rem;
    }
    .about-me-tab-div{
        display: block;
    }
    .about-me-tab-div div{
        width: 90%;
    }
}
.contact{
    background-color: #434342;
    background-color: black;
    color: white;
    text-align: center;
    padding: 4rem;
    padding-left: 5rem;
    padding-right: 5rem;
}
.contact h1{
    padding-bottom:1rem;
}
.contact hr{
    margin-top:2.5rem;
    margin-bottom:2.5rem;
}
.contact-form{
    padding: 1rem;
}
.contact-form  input{
    background-color: black;
    border: none;
    color: white;
    border-bottom: 1px solid white;
    margin-bottom: 1rem;
    padding: 0.6rem;
