}
.contact-div{
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.contact-information a{
    margin:auto;
    padding-right:1rem ;
}
@media screen and (max-width: 500px) {
    .contact{
        padding: 2rem;
        padding-left: 1rem;
        padding-right: 1rem;
    }
    .contact-div{
        display: block;
        padding: 0.5rem;
    }
}
.education{
    background-color: #4e54c8;
    color: white;
    text-align: left;
    padding: 4rem;
    padding-left: 5rem;
    padding-right: 5rem;
    text-align: center;
}

.education h1{
    text-align: center;
    padding-bottom:1rem;
}
.education hr{
    margin-top:2.5rem;
    margin-bottom:2.5rem;
}

.education-tab-div{
    display: flex;
    justify-content: space-around;
    text-align: left;
}
.education-tab-div div{
    padding: 1rem;
}
.education-course{
    width : 25%;
}
.education-details{
    width : 50%;
}
@media screen and (max-width: 500px) {
    .education{
