.projects{
    background-color: #4e54c8;
    color: white;
    text-align: center;
    padding: 4rem;
    padding-left: 5rem;
    padding-right: 5rem;
}
.projects h1{
    padding-bottom:1rem;
}
.projects hr{
    margin-top:2.5rem;
    margin-bottom:2.5rem;
}
.projects-div{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 4rem;
    grid-row-gap: 2rem;
    text-align: center !important;
    height: 100%;
}
.display-projects-div{
    margin: auto;
    padding: 2rem;
    width: 70%;
    background-color:#fefefa;
    color: #4e54c8;
    border-radius: 1rem;
    height: 100%; 
}
.display-projects-div:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.display-projects-div h4:first-of-type {
    float: right !important;
}

.display-projects-div h4:last-of-type {
    float: left !important;
}
.display-projects-technology-div{
    display: flex;
    flex-wrap: wrap;
    justify-content: left;

}
.display-technology-div{
    border:2px solid #4e54c8;
    border-radius: 1rem;
    margin-right: 1rem;
    margin-top: 1rem;
}
.display-technology-div:hover{
    background-color:#4e54c8;
    color: white;
}
.display-technology-div span{
    padding: 1rem;
}
.display-projects-link-div{
    text-align: right;
    padding: 0;
    margin: 0;
}
.display-projects-link-div Button{
    color: white;
    border-color: #4e54c8;
    background-color: #4e54c8;
    border-radius: 2rem;
    margin: 0;
}
.display-projects-link-div Button:hover{
    background-color: white;
    color: #4e54c8;
}
@media screen and (max-width: 500px) {
    .projects{
        padding: 2rem;
        padding-left: 1rem;
        padding-right: 1rem;
    }
    .projects-div{
        display: block;
    }
    .display-projects-div{
        width: 90%;
        margin: 1rem;

    }
}
.abilities{
    background-color: white;
    color: #4e54c8;
    text-align: center;
    padding: 4rem;
    padding-left: 5rem;
    padding-right: 5rem;
}
.abilities h1{
    padding-bottom:1rem;
}
.abilities h4{
    text-align: left;
    padding-bottom:1rem;
    padding-left: 1rem;
}
.abilities hr{
    margin-top:2.5rem;
    margin-bottom:2.5rem;
}
.abilities-tab-div{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 5rem;
}
.display-ability{
    padding-left: 1rem;
    padding-right: 1rem;
}
.display-ability:hover{
    background-color: #e9e9e9;
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}

.display-ability-name{
    padding-left: 1rem;
    float: left;
}
.display-ability-rating{
    float: right;
    padding-left: 2rem;
}
@media screen and (max-width: 500px) {
    .abilities{
        padding: 2rem;
        padding-left: 1rem;
        padding-right: 1rem;
    }
    .abilities-tab-div{
        display: block;
    }
    .display-ability{
        display: grid;
        grid-template-columns: 1fr 2fr;
        padding-bottom: 0.2rem;
    }
}
